@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
body{margin:0;padding:0;height:100%}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App{text-align:center;height:100%;text-align:left;font-family:'Lato', sans-serif;font-weight:100;box-sizing:inherit}.App.dark{background-color:#2B2D2F;color:white}

.intro{padding:120px 100px;height:calc(100vh - 240px);display:flex;flex-direction:column;justify-content:space-around}@media screen and (max-width: 500px){.intro{padding:10px 10px;height:calc(100vh - 20px)}}.name{font-weight:bold}h1{font-weight:100;font-size:3.5rem}@media screen and (max-width: 1500px){h1{font-size:3.5rem}}@media screen and (max-width: 500px){h1{font-size:2.5rem}}@media screen and (max-width: 320px){h1{font-size:1.5rem}}h2{font-weight:100;max-width:700px;font-size:2rem}@media screen and (max-width: 1500px){h2{font-size:2rem}}@media screen and (max-width: 500px){h2{font-size:1.5rem}}@media screen and (max-width: 320px){h2{font-size:1rem}}h3{font-weight:100;font-size:1.5rem}@media screen and (max-width: 1500px){h3{font-size:1.5rem}}@media screen and (max-width: 500px){h3{font-size:1.5rem}}@media screen and (max-width: 320px){h3{font-size:1rem}}.switch{position:absolute;top:40px;right:40px;width:60px;height:34px}.switch input{opacity:0;width:0;height:0}.slider{position:absolute;cursor:pointer;top:0;left:0;right:0;bottom:0;background-color:#ccc;-webkit-transition:0.4s;transition:0.4s}.slider:before{position:absolute;content:"ðŸŒž";height:40px;width:40px;left:0px;bottom:4px;top:0;bottom:0;margin:auto 0;-webkit-transition:0.4s;transition:0.4s;box-shadow:0 0px 15px #2020203d;background:white;background-repeat:no-repeat;background-position:center;display:flex;justify-content:center;align-items:center;font-size:x-large}input:checked+.slider{background-color:#2196f3}input:focus+.slider{box-shadow:0 0 1px #2196f3}input:checked+.slider:before{-webkit-transform:translateX(24px);transform:translateX(24px);background:white;content:"ðŸŒ™";background-repeat:no-repeat;background-position:center}.slider.round{border-radius:34px}.slider.round:before{border-radius:50%}

footer{display:flex;justify-content:flex-end;padding:120px 100px}@media screen and (max-width: 500px){footer{padding:40px 20px}}@media screen and (max-width: 320px){footer{padding:20px 10px}}.social_links{min-width:400px;display:flex;justify-content:space-evenly;font-size:1.3rem}@media screen and (max-width: 500px){.social_links{font-size:1rem;min-width:100%}}@media screen and (max-width: 320px){.social_links{font-size:.9rem;min-width:100%}}

.background_section{padding:120px 100px;display:flex;width:calc(100% - 200px);visibility:visible;opacity:0;-webkit-transition:opacity 1s ease-in;transition:opacity 1s ease-in}@media screen and (max-width: 500px){.background_section{width:100%;padding:0}}@media screen and (max-width: 320px){.background_section{width:100%;padding:0;flex-direction:column}}.background_section.visible{opacity:1}.section_name{width:30%;color:#4CAF50;font-weight:500;text-align:right;margin:0px 70px 0px 0px;font-size:1.4rem}@media screen and (max-width: 320px){.section_name{width:calc(100% - 20px);text-align:inherit;padding:20px 10px}}.section_info_background{width:70%}@media screen and (max-width: 320px){.section_info_background{width:calc(100% - 20px);padding:0px 10px}}.text{font-size:1.3rem;padding-bottom:20px}@media screen and (max-width: 1500px){.text{font-size:1.3rem}}@media screen and (max-width: 500px){.text{font-size:1.3rem}}@media screen and (max-width: 320px){.text{font-size:1rem}}a{text-decoration:none;font-weight:600;color:inherit}

.fade_section{padding:120px 100px;display:flex;width:calc(100% - 200px);visibility:visible;opacity:0;-webkit-transition:opacity 0.8s ease-in;transition:opacity 0.8s ease-in}@media screen and (max-width: 500px){.fade_section{width:100%;padding:20;flex-direction:column}}@media screen and (max-width: 320px){.fade_section{width:100%;padding:0;flex-direction:column}}.fade_section.visible{opacity:1}

.slillz_info{display:flex;flex-direction:row;width:70%;justify-content:space-between}@media screen and (max-width: 320px){.slillz_info{width:calc(100% - 20px);text-align:inherit;padding:20px 10px}}.category_name{font-size:1.3rem;font-weight:500}

.download_section{display:flex;width:100%;align-items:center;padding:120px 0px;justify-content:center}.button{width:220px;height:70px;text-align:center;display:flex;align-items:center;justify-content:center;border:2px solid #4CAF50}.button:hover{background:lightgreen}

.title{font-weight:100;font-size:1.5rem;font-weight:400}@media screen and (max-width: 1500px){.title{font-size:1.5rem}}@media screen and (max-width: 500px){.title{font-size:1.2rem}}@media screen and (max-width: 320px){.title{font-size:1rem}}.Job_info{width:70%}@media screen and (max-width: 320px){.Job_info{width:calc(100% - 20px);padding:0px 10px}}

.scroll{position:fixed;bottom:40px;right:40px;font-size:2rem}.scroll:hover{cursor:pointer}

.pro_image{width:100%;height:auto}.pro_text{width:200px;min-width:200px;margin-right:40px}.pro_img_text{display:flex;margin-bottom:40px}@media screen and (max-width: 1000px){.pro_img_text{padding:0;flex-direction:column}}@media screen and (max-width: 320px){.pro_img_text{padding:0;flex-direction:column}}.pro_figure{max-width:700px}.pro{overflow:hidden;width:100%}@media screen and (max-width: 1000px){.pro{width:100%}}.cusrom_src{font-weight:200}.src_container{display:flex;flex-direction:column;margin-bottom:20px}.tec_container{display:flex;margin-right:10px;flex-flow:wrap}@media screen and (max-width: 320px){.tec_container{width:100%;margin:0px}}.technology{padding:5px;margin:2px;border:2px solid #4CAF50;border-radius:20px}

